</template>

<script>
import Snap from 'snapsvg-cjs'

import FormatoResta from "../actvs/actvshijos/FormatoResta.vue"
import RestaInput from "../actvs/actvshijos/RestaInput.vue"

export default {
    name: "ProblemaCombinacion",
    data: function () {
        return {
            mostrarIntro: true,
            minAct: null,
            susAct: null,
            quitoAct: null,
            x: 40,
            y: 0,   // Asiento disponible
            resta: 0,   // Asiento ocupado
            s: null, // Para el canvas Snap
            asientos: [],
            asientosO: [],
            contador: 0,
            preguntarResta: false,
        }
    },
    components: {
        FormatoResta,
        RestaInput
    },
    methods: {
        actualizarObj(minuendo,sustraendo,quito) {
            this.minAct = minuendo;
            this.susAct = sustraendo;
            this.quitoAct = quito;
            this.irBorrando(this.quitoAct);
            this.preguntaShow() // Para mostrar la pregunta de la resta al final
        },
        randomF(min,max) {
            let i = Math.floor((Math.random() * (max - min + 1)) + min);
            return i;
        },
        generaRamdon(){
            this.mostrarIntro = false;
            this.y = this.randomF(1,35);
            this.resta = this.x - this.y;
            this.dibujaRamdon();
        },
        dibujaRamdon(){
            let cuantos = this.resta;
            let arrayAO = [];   // Este es un array con los asientos ocupados. El codigo que sigue es para hacerlos aleatorios y obtener el id de asiento y poder pintar de rojo.
            while (arrayAO.length < cuantos) {
                let tempo = this.randomF(0,39);
                let existe = false;
                for (let j = 0; j < arrayAO.length; j++) {
                    if (arrayAO[j]==tempo) {
                        existe = true;
                        break;
                    }
                }
                if (!existe) {
                    arrayAO.push(tempo); // construimos el array
                    this.asientos[tempo].attr({fill:"red"}); // pintamos de rojo los asientos ocupados
                }
            }
            // Ahora clonamos el arrayAO a un array de la data asientosO
            this.asientosO = arrayAO.slice();
        },
        irBorrando(menos){
            this.contador += menos;
            for (let i = 0; i < this.contador; i++) {
                this.asientos[this.asientosO[i]].attr({fill:"none",stroke:"gray"});
            }
        },
        preguntaShow() {
            if (this.susAct == 0 && this.minAct == this.y) {
                this.preguntarResta = true;
                this.s.text(12,25,'¡Correcto!').attr({'font-size':'0.4rem','font-weight':'bold','fill':'green'});
                this.s.text(12,30,'Esos únicos asientos son los').attr({'font-size':'0.18rem'});
                this.s.text(12,35,'vacios. Responde la pregunta').attr({'font-size':'0.18rem'});
            }
            return
        }
    },
    mounted: function() {
        let sC = Snap("#svg2");
        this.s = sC;
        let asiento = Snap("#rect");
        for (let i = 0; i < 40; i++) {
            this.asientos.push(asiento.clone());
        }
        asiento.attr({fill:"none",stroke:"none"});
        this.asientos[0].transform("t15,40");
        this.asientos[1].transform("t20,40");
        this.asientos[2].transform("t35,40");
        this.asientos[3].transform("t40,40");
        this.asientos[4].transform("t15,47");
        this.asientos[5].transform("t20,47");
        this.asientos[6].transform("t35,47");
        this.asientos[7].transform("t40,47");
        this.asientos[8].transform("t15,54");
        this.asientos[9].transform("t20,54");
        this.asientos[10].transform("t35,54");
        this.asientos[11].transform("t40,54");
        this.asientos[12].transform("t15,61");
        this.asientos[13].transform("t20,61");
        this.asientos[14].transform("t35,61");
        this.asientos[15].transform("t40,61");
        this.asientos[16].transform("t15,68");
        this.asientos[17].transform("t20,68");
        this.asientos[18].transform("t35,68");
        this.asientos[19].transform("t40,68");
        this.asientos[20].transform("t15,75");
        this.asientos[21].transform("t20,75");
        this.asientos[22].transform("t35,75");
        this.asientos[23].transform("t40,75");
        this.asientos[24].transform("t15,82");
        this.asientos[25].transform("t20,82");
        this.asientos[26].transform("t35,82");
        this.asientos[27].transform("t40,82");
        this.asientos[28].transform("t15,89");
        this.asientos[29].transform("t20,89");
        this.asientos[30].transform("t35,89");
        this.asientos[31].transform("t40,89");
        this.asientos[32].transform("t15,96");
        this.asientos[33].transform("t20,96");
        this.asientos[34].transform("t35,96");
        this.asientos[35].transform("t40,96");
        this.asientos[36].transform("t15,103");
        this.asientos[37].transform("t20,103");
        this.asientos[38].transform("t35,103");
        this.asientos[39].transform("t40,103");
    }

}
</script>


